<style>



</style>

<template>
<div>
  <div class="ui container segment">
    <banner :space="space"></banner>
    <div class="ui grid">
      <div class="ui inverted dimmer" :class="{active: loading}">
        <div class="ui large text loader">Loading</div>
        <p></p>
        <p></p>
        <p></p>
      </div>
      <spacetable :space="space"></spacetable>
    </div>
    <div class="ui grid">
      <div class="ui right aligned column">
        <export-space :space="space"></export-space>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import Vue from 'vue'
import spacetable from './spaceTable.vue'
import banner from './banner.vue'
import exportSpace from './exportSpace.vue'
import {
  mapState
} from 'vuex'

Vue.component('spacetable', spacetable)
Vue.component('banner', banner)
Vue.component('exportSpace', exportSpace)

export default {
  data() {
    return {
      loading: true
    }
  },
  props: ['space'],
  computed: mapState(['loaderColor', 'loaderSize']),
  mounted: function() {
    this.$events.$on('vuetable:loaded', eventData => this.loading = false)
    this.$events.$on('vuetable:loading', eventData => this.loading = true)
  }
}
</script>



