<style>



</style>

<template>
<div class="ui text container raised">
  <div class="ui top attached block header segment">
    <h1 class="ui header center aligned">Available FAQ Spaces</h1>
  </div>
  <div class="ui bottom attached segment very padded">
    <div class="ui inverted dimmer" :class="{active: loading}">
      <div class="ui large text loader">Loading</div>
      <p></p>
      <p></p>
      <p></p>
    </div>
    <div class="ui divided big list">
      <div class="item" v-for="space in userSpaces">
        <i class="comment outline icon"></i>
        <div class="middle aligned content">
          <router-link class="ui header" v-bind:to="'/space/' + space._id"><a>{{space.displayName}}</a></router-link>
          <div class="description">
            <p></p>
          </div>
        </div>
        <export-space :space="space._id"></export-space>
      </div>
    </div>
    <p v-if="userSpaces.length < 1">No Available FAQ Rooms</p>
    <p v-if="userSpaces.length < 1">Please tag Inquire with a question in a Space. </p>
  </div>
</div>
</template>

<script>
import {
  mapState,
  mapActions
}
from 'vuex'
import exportSpace from './exportSpace.vue'

export default {
  data() {
    return {
      loading: true
    }
  },
  components: {
    exportSpace
  },
  computed: mapState(['isAuthenticated', 'userSpaces', 'loaderColor', 'loaderSize']),
  methods: mapActions(['loadSpaces']),
  mounted: function() {
    this.loadSpaces()
  },
  watch: {
    userSpaces: function(array) {
      if (array)
        this.loading = false
    }
  }
}
</script>



